export default {
  name: "ks-image",
  props: {
	 radius:{
		 type:String,
		 default:'0'
	 },
    /**图片地址 */
    src: String,
    /**图片类型 defailt | user | logo */
    type: String,
    /*图片如何适应到容器框*/
    aspect: {
      type: String,
      default: "aspectFill"
    },
    /**经过效果 scale */
    hover:String
  },
  data() {
	  var baseUrl = this.$store.state.SRC+"/config/app"
    var _defaultImg = baseUrl+"/static/images/common/default-img.png";
    switch (this.type) {
      case "user":
        _defaultImg = baseUrl+"/static/images/common/default-avatar.png";
        break;
      case "logo":
        _defaultImg = baseUrl+"/static/images/common/default-logo.png?2.0";
        break;
		case "teacher":
		  _defaultImg = baseUrl+"/static/images/common/default-teacher.png";
		  break;
    }
    return {
      imgSrc: this.src,
      defaultImg: _defaultImg
    };
  },
  methods: {
    /**
     * 处理图片加载出错
     */
    onerror() {
      this.imgSrc = this.defaultImg;
    }
  },
  watch: {
    src(_imgSrc) {
      this.imgSrc = _imgSrc;
    }
  }
};
